import { useCallback, useEffect, useState } from 'react';
import type { WebAppInitData, WebAppUser } from '../types/max-bridge';

//   
const SUPPORTED_FEATURES = {
  haptic: false, //       -
  backButton: true,
  openLink: true,
  share: false,
  requestContact: false,
};

export const useMaxBridge = () => {
  const [isMaxApp, setIsMaxApp] = useState(false);
  const [initData, setInitData] = useState<WebAppInitData | null>(null);
  const [user, setUser] = useState<WebAppUser | null>(null);
  const [isReady, setIsReady] = useState(false);

  // ,     MAX
  useEffect(() => {
    const isMaxEnvironment = !!window.WebApp;
    setIsMaxApp(isMaxEnvironment);

    if (isMaxEnvironment && window.WebApp) {
      const webApp = window.WebApp;

      //   
      setInitData(webApp.initDataUnsafe);
      setUser(webApp.initDataUnsafe?.user || null);

      //  MAX,   
      webApp.ready();
      setIsReady(true);

      console.log(' MAX Bridge initialized:', {
        platform: webApp.platform,
        version: webApp.version,
        user: webApp.initDataUnsafe?.user,
        supportedFeatures: SUPPORTED_FEATURES,
      });
    }
  }, []);

  //   
  const getStartParam = useCallback((): string | null => {
    if (initData?.start_param) {
      return initData.start_param;
    }

    if (typeof window !== 'undefined') {
      const urlParams = new URLSearchParams(window.location.search);
      return urlParams.get('startapp') || urlParams.get('start_param');
    }

    return null;
  }, [initData]);

  //   
  const parseStartParam = useCallback(<T = unknown>(): T | null => {
    const startParam = getStartParam();
    if (!startParam) return null;

    try {
      if (startParam.startsWith('{')) {
        return JSON.parse(startParam) as T;
      }

      const params = new URLSearchParams(startParam);
      const result: Record<string, boolean | number | string> = {};

      params.forEach((value, key) => {
        if (value === 'true') result[key] = true;
        else if (value === 'false') result[key] = false;
        else if (!isNaN(Number(value)) && value !== '') result[key] = Number(value);
        else result[key] = value;
      });

      return result as T;
    } catch (error) {
      console.error('Error parsing start param:', error);
      return null;
    }
  }, [getStartParam]);

  //    
  const closeApp = useCallback(() => {
    if (window.WebApp?.close) {
      window.WebApp.close();
    }
  }, []);

  //   "" - 
  const showBackButton = useCallback((show: boolean) => {
    if (window.WebApp?.BackButton) {
      if (show) {
        window.WebApp.BackButton.show();
      } else {
        window.WebApp.BackButton.hide();
      }
    }
  }, []);

  const onBackButtonClick = useCallback((callback: () => void) => {
    if (window.WebApp?.BackButton?.onClick) {
      window.WebApp.BackButton.onClick(callback);
    }
  }, []);

  //     -    -
  const hapticFeedback = useCallback(
    async (
      type: 'impact' | 'notification' | 'selection',
      style?: 'light' | 'medium' | 'heavy' | 'soft' | 'rigid',
    ) => {
      if (!window.WebApp?.HapticFeedback) {
        console.log(' Haptic feedback not supported in web version');
        return;
      }
      if (!style) {
        await window.WebApp.HapticFeedback.impactOccurred('medium');
        return;
      }

      try {
        switch (type) {
          case 'impact':
            await window.WebApp.HapticFeedback.impactOccurred(style);
            break;
          case 'notification':
            await window.WebApp.HapticFeedback.notificationOccurred('success');
            break;
          case 'selection':
            await window.WebApp.HapticFeedback.selectionChanged();
            break;
          default:
            await window.WebApp.HapticFeedback.impactOccurred('medium');
        }
        console.log(' Haptic feedback:', type);
      } catch (error) {
        console.warn(' Haptic feedback failed:', error);
        //   ,  
      }
    },
    [],
  );

  //    - 
  const openExternalLink = useCallback((url: string) => {
    if (window.WebApp?.openLink) {
      window.WebApp.openLink(url);
    } else {
      window.open(url, '_blank');
    }
  }, []);

  return {
    // 
    isMaxApp,
    isReady,
    initData,
    user,
    supportedFeatures: SUPPORTED_FEATURES,

    //     
    getStartParam,
    parseStartParam,

    //   Bridge
    closeApp,
    showBackButton,
    onBackButtonClick,
    hapticFeedback,
    openExternalLink,

    //    WebApp
    webApp: window.WebApp,
  };
};
